<template>
    <div>
        <div class="kd-page-title">
            身份比对
        </div>
        <div class="kd-content-wrapper cardWrap">
            <div class="photoWrap">
                <img
                    class="binocularCamera"
                    :src="binocularCamera"
                >
                <div class="identityPic">
                    <img :src="identifyCard.identityPic">
                    <p v-html="identifyCard.partyName"/>
                </div>
            </div>
            <div class="text">
                <span class="el-icon-loading"/>正在进行身份比对，请耐心等待......
            </div>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { checkIdInfo } from '@/service/common';
import { cancelPrefix } from '@/utils/tools';

export default {
    name: 'RecordedMotorVehiclesComparison',
    computed: {
        ...mapState('recordedMotorVehicles', {
            identityPic: 'identityPic',
            binocular: 'binocular',
            partyName: 'partyName',
            certNumber: 'certNumber'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        async checkPerson () {
            if (!this.CONSTANT.disableContrast) {
                const res = await checkIdInfo({
                    sfzmhm: this.certNumber,
                    xm: this.partyName,
                    photo: cancelPrefix(this.binocular),
                    identityPic: cancelPrefix(this.identityPic)
                });
                if (res && res.result && res.result.respData && res.result.respData.code === '0') {
                    this.changeStep('down');
                } else {
                    this.changeTipPage({
                        message: '检测结果与身份证信息不匹配，请您重新检测…',
                        imgName: 'imgTexts'
                    });
                }
            } else {
                this.changeStep('down');
            }
        }
    },
    async mounted () {
        this.checkPerson();
    }

};
</script>

<style lang="scss" scoped>
    .cardWrap {
        .photoWrap {
            display: flex;
            overflow: hidden;
            width: 90%;
            height: 70%;
            justify-content: space-around;

            .binocularCamera {
                background: url("../../../../../assets/images/loading.gif") center no-repeat;
                height: 100%;
            }

            .identityPic {
                width: 20%;
                height: 100%;

                img {
                    background: url("../../../../../assets/images/loading.gif") center no-repeat;
                    width: 100%;
                }

                p {
                    text-align: center;
                }
            }
        }

        .text {
            margin-top: 3vh;
            font-size: 2.8vh;
            text-align: center;
        }
    }

</style>
