<template>
    <div>
        <identify-card-page
            v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"
            :disable-input="true"
        />
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="getBusinessId"
        />
        <choice-car v-if="currentStep === steps.findIndex(step=>step.key === 'choiceCar')"/>
        <relation-type v-if="currentStep === steps.findIndex(step=>step.key === 'relationType')"/>
        <card v-if="currentStep === steps.findIndex(step=>step.key === 'card')"/>
        <binocular v-if="currentStep === steps.findIndex(step=>step.key === 'binocular')"/>
        <comparison v-if="currentStep === steps.findIndex(step=>step.key === 'comparison')"/>
        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="presented"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="data"
            :mp3="audioCar"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { cancelPrefix } from '@/utils/tools';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import complete from '@/components/complete';

import relationType from './components/relation-type';
import choiceCar from './components/choice-car';
import card from './components/card';
import binocular from './components/binocular';
import comparison from './components/comparison';
import audioCar from '@/assets/media/trafic/7.mp3';

import { getBusinessId, check, upload } from '@/service/traffic/vehicle/recordedMotorVehicles';

// 备案非本人机动车
export default {
    name: 'RecordedMotorVehiclesIndex',
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        relationType,
        choiceCar,
        process,
        card,
        binocular,
        comparison,
        complete
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('recordedMotorVehicles', {
            data: 'result',
            identityPic: 'identityPic',
            binocular: 'binocular',
            name: 'partyName',
            cert: 'certNumber',
            carType: 'carType',
            carLicense: 'carLicense',
            relationship: 'relationship'
        })
    },
    data () {
        return {
            audioCar: audioCar
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setSteps']),
        ...mapMutations('recordedMotorVehicles', ['setResult']),
        async getBusinessId () {
            const res = await getBusinessId({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async presented () {
            const res = await check({
                jszh: this.identifyCard.certNumber,
                xm: this.identifyCard.partyName,
                hpzl: this.carType,
                hphm: this.CONSTANT.carTypeName + this.carLicense,
                sfzmhm: this.cert,
                czgx: this.relationship
            });
            if (res && res.code === '0') {
                this.upload();
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async upload () {
            const res = await upload({
                jszh: this.identifyCard.certNumber,
                xm: this.identifyCard.partyName,
                hpzl: this.carType,
                hphm: this.CONSTANT.carTypeName + this.carLicense,
                sfzmhm: this.cert,
                czgx: this.relationship,
                jsrxczp: cancelPrefix(this.binocularCamera),
                jsrtxzp: cancelPrefix(this.identifyCard.identityPic),
                czxczp: cancelPrefix(this.binocular),
                cztxzp: cancelPrefix(this.identityPic)
            });
            if (res && res.code === '0') {
                this.setResult(res.result);
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }

    },
    mounted () {
        this.setSteps([
            { name: '身份识别', isActive: true, visible: true, key: 'identifyCardPage' },
            { name: '身份识别', isActive: false, visible: false, key: 'faceYitu' },
            { name: '身份识别', isActive: false, visible: false, key: 'contrast' },
            { name: '选择机动车', isActive: false, visible: true, key: 'choiceCar' },
            { name: '备案非本人机动车', isActive: false, visible: true, key: 'relationType' },
            { name: '车主信息', isActive: false, visible: true, key: 'card' },
            { name: '车主信息', isActive: false, visible: false, key: 'binocular' },
            { name: '车主信息', isActive: false, visible: false, key: 'comparison' },
            { name: '车主信息', isActive: false, visible: false, key: 'process' },
            { name: '完成提交', isActive: false, visible: true, key: 'complete' }
        ]);
    }
};

</script>
