<template>
    <div>
        <div class="kd-page-title">
            车主身份识别
        </div>
        <div class="kd-content-wrapper carryingCard">
            <img
                class="staticCard"
                :src="cardNoBg"
            >
            <img
                class="moveCard"
                :src="cardMove"
            >
            <div
                class="text"
                v-html="tips"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="()=>{this.changeStep('up');}"
            />
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { wsSend, wsOnMessage } from '@/utils/websocket';
import { cardReadData } from '@/utils/tools';
import cardNoBg from '@/assets/images/cardno_bg.png';
import cardMove from '@/assets/images/cardMove.gif';
import audioCar from '@/assets/media/trafic/5.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'RecordedMotorVehiclesCard',
    props: ['disableInput'],
    computed: {
        ...mapState('permanent', {
            identifyWebSocket: 'identifyWebSocket',
            lightWebSocket: 'lightWebSocket',
            cardReaderType: 'cardReaderType'
        })
    },
    data () {
        return {
            cardNoBg: cardNoBg,
            cardMove: cardMove,
            tipShowFlag: false,
            identify: {},
            tips: '请将二代身份证放置身份证阅读区'
        };
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.startReadCard();
    },
    beforeDestroy () {
        this.audioCar.destroy();
        wsOnMessage(this.identifyWebSocket, null);
        wsSend(this.lightWebSocket, 'lightCtrl@B4:OFF');
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setIdentifyCard', 'setBinocularCamera']),
        ...mapMutations('recordedMotorVehicles', ['setUserInfo']),
        startReadCard () {
            wsSend(this.lightWebSocket, 'lightCtrl@B4:ON');
            wsOnMessage(this.identifyWebSocket, (res) => {
                if (res && res.code === 1) {
                    console.log('身份证读卡器：', res);
                    const data = cardReadData(res.result, this.cardReaderType);
                    this.setUserInfo({
                        identityPic: data.identityPic,
                        certNumber: data.certNumber,
                        partyName: data.partyName
                    });
                    this.changeStep('down');
                } else {
                    setTimeout(() => {
                        wsSend(this.identifyWebSocket, { directive: 'ReadIdCard' });
                    }, 1000);
                }
            });
            wsSend(this.identifyWebSocket, { directive: 'play' });
            wsSend(this.identifyWebSocket, { directive: 'ReadIdCard' });
        }
    }
};
</script>

<style lang="scss" scoped>
    .checkTips {
        width: 60%;
        height: 40px;
        line-height: 40px;
        color: red;
    }

    .hide {
        visibility: hidden;
    }

    .show {
        visibility: visible;
    }

    .notCarryingCard {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        img {
            transform: scale(-1, 1);
            max-height: 22.5vh;
            max-width: 80%;
            margin: 20px 0;
        }
    }

    .inputClass {
        display: inline-block;
        width: 60%;
        font-size: 26px;
        color: #636F93;
        margin: 10px 0;
    }

    .carryingCard {
        position: relative;

        .staticCard {
            transform: scale(-1, 1);
            width: auto;
            height: 312px;
        }
    }

    .moveCard {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
    }

    .text {
        font-size: 2.8vh;
        text-align: center;
        margin-top: 3vh;
    }
</style>
