<template>
    <div>
        <div class="kd-page-title">
            请输入机动车信息
        </div>
        <div class="kd-page-content-wrapper">
            <el-form
                class="uploadLabel search-form"
                label-width="15vw"
            >
                <el-form-item label="与车主关系：">
                    <el-radio-group v-model="searchType">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in searchTypes"
                            :key="index"
                            :label="index"
                            border
                        >
                            {{ item }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import audioCar from '@/assets/media/trafic/4.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'RecordedMotorVehiclesRelationType',
    data () {
        return {
            searchType: '3',
            searchTypes: {
                1: '父母',
                2: '配偶',
                3: '子女',
                4: '亲戚',
                5: '朋友',
                6: '其他'
            }
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        ...mapMutations('recordedMotorVehicles', ['setRelationship']),
        nextStep () {
            const types = {
                1: '0001',
                2: '0002',
                3: '0003',
                4: '0004',
                5: '0005',
                6: '0099'
            };
            this.setRelationship(types[this.searchType]);
            this.changeStep('down');
        },
        preStep () { // 上一步
            this.changeStep('up');
        }
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    }
};
</script>

<style lang="scss" scoped>
    .search-form {
        margin: 7.5vw 2vw auto;
    }

    .uploadLabel .uploadChoose {
        width: 18.8vw !important;
    }

    .el-radio.is-bordered {
        margin: 0 20px 20px 0 !important;
    }
</style>
