<template>
    <div>
        <div class="kd-page-title">
            车主人脸照片采集
        </div>
        <div class="kd-content-wrapper wrap-info">
            <div
                v-if="!isShowCropper"
                class="imgAppear"
            >
                <img :src="binocularCamera">
            </div>
            <div
                v-if="isShowCropper"
                class="vue-cropper-box"
            >
                <div class="vue-cropper-content">
                    <vue-cropper
                        ref="cropper"
                        :img="option.img"
                        :output-size="option.outputSize"
                        :output-type="option.outputType"
                        :info="option.info"
                        :can-scale="option.canScale"
                        :auto-crop="option.autoCrop"
                        :auto-crop-width="option.autoCropWidth"
                        :auto-crop-height="option.autoCropHeight"
                        :fixed="option.fixed"
                    />
                </div>
            </div>
            <div class="text">
                {{ tip }}
            </div>
        </div>
        <!-- vueCropper 剪裁图片实现-->
        <div class="kd-foot-button kd-foot-button-position" v-if="openFailed && !$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { VueCropper } from 'vue-cropper'; // 裁剪插件
import { wsSend, wsOnMessage } from '@/utils/websocket';
import { addPrefix } from '@/utils/tools';
import audioCar from '@/assets/media/trafic/6.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'RecordedMotorVehiclesBinocular',
    components: { VueCropper },
    computed: {
        ...mapState('permanent', {
            binocularWebSocket: 'binocularWebSocket'
        })
    },
    data () {
        return {
            tip: '正在打开摄像头，请稍候......',
            openFailed: false,
            binocularCamera: '',
            binocularCameraBlack: '',
            isShowCropper: false,
            // 裁剪组件的基础配置option
            option: {
                img: '', // 裁剪图片的地址
                info: false, // 裁剪框的大小信息
                outputSize: 0.9, // 裁剪生成图片的质量
                outputType: 'jpeg', // 裁剪生成图片的格式
                canScale: false, // 图片是否允许滚轮缩放
                autoCrop: false, // 是否默认生成截图框
                full: true
                // fixed: true                    // 是否开启截图框宽高固定比例
                // fixedNumber: [1, 1]              // 截图框的宽高比例
            },
            showObj: true
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('recordedMotorVehicles', ['setUserInfo']),
        initFaceDevice () {
            this.tip = '请正对摄像头，脸部置于框内保持不动';
            wsOnMessage(this.binocularWebSocket, this.getImg);
            wsSend(this.binocularWebSocket, {
                module: 'YiTu',
                directive: 'FaceDualCapture'
            });
        },
        getImg (res) {
            if (res && res.code === 1) {
                if (res.directive === 'FaceDualCapture') {
                    this.binocularCamera = addPrefix(res.result.visibleImage);
                    this.binocularCameraBlack = addPrefix(res.result.infraredImage);
                } else if (res.directive === 'ReOpen') {
                    // console.log('依图双目摄像头：', res);
                    wsSend(this.binocularWebSocket, {
                        module: 'YiTu',
                        directive: 'FaceDualCapture'
                    });
                } else if (res.directive === 'OpenSuccess') {
                    console.log('依图双目摄像头打开成功');
                } else if (res.directive === 'FaceDualTip') {
                    this.tip = res.result;
                } else {
                    console.log('依图双目完成拍摄');
                    console.info('依图双目摄像头：', res);
                    wsSend(this.binocularWebSocket, {
                        module: 'YiTu',
                        directive: 'Stop'
                    });
                    this.isShowCropper = true;
                    this.tip = '拍照成功！';
                    this.binocularCamera = addPrefix(res.result.visibleImage);
                    this.option.img = this.binocularCamera;
                    setTimeout(() => {
                        this.onCubeImg();
                    }, 2000);
                }
            } else {
                console.log('依图双目摄像头：', res);

                if (res.directive === 'ReOpen') {
                    this.changeTipPage({
                        message: '系统开小差了，请重试！',
                        enableBack: true
                    });
                } else if (res.directive === 'FaceDualCapture') {
                    wsSend(this.binocularWebSocket, {
                        module: 'YiTu',
                        directive: 'ReOpen'
                    });
                }
            }
        },
        // 确定裁剪图片
        onCubeImg () {
            // 获取cropper的截图的base64 数据
            this.$refs.cropper.getCropData((data) => {
                // // 先将显示图片地址清空，防止重复显示
                this.option.img = '';
                // 将剪裁后的图片执行下一步传递
                const photoSize = this.showPhotoSize(data);
                console.log('裁剪后文件大小 --' + photoSize + 'KB');
                this.setUserInfo({
                    binocular: data
                });
                window.document.onclick();
                this.changeStep('down');
            });
            this.$refs.cropper.startCrop();
        },
        // 计算图片大小
        showPhotoSize (str) {
            let baseStr = str;
            const tag = 'base64,';
            baseStr = baseStr.substring(baseStr.indexOf(tag) + tag.length);
            // 去掉‘=’
            const eqTagIndex = baseStr.indexOf('=');
            baseStr = eqTagIndex !== -1 ? baseStr.substring(0, eqTagIndex) : baseStr;
            const strLen = baseStr.length;
            return (strLen - (strLen / 8) * 2) / 1024;
        }
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.initFaceDevice();
    },
    beforeDestroy () {
        this.audioCar.destroy();
        wsOnMessage(this.binocularWebSocket, null);
        wsSend(this.binocularWebSocket, {
            module: 'YiTu',
            directive: 'Stop'
        });
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        .imgAppear {
            display: flex;

            img {
                background: url("../../../../../assets/images/loading.gif") center no-repeat;
                margin: 0 1vw;
                min-width: 225px;
                height: 400px;
            }
        }

        .text {
            font-size: 2.8vh;
            text-align: center;
            margin-top: 1vh;
        }

        ::v-deep .cropper-view-box {
            outline: none;
        }

        ::v-deep .crop-point {
            visibility: hidden;
        }

        .vue-cropper-content {
            width: 300px;
            height: 300px;
        }
    }

</style>
