<template>
    <div>
        <div class="kd-page-title">
            输入车牌
        </div>
        <div class="kd-page-content-wrapper wrap-info">
            <car-input
                :get-car-input="getCarInput"
                :last-input="false"
                :mp3="audioCar"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!carLicense"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import carInput from '@/components/car-input';
import audioCar from '@/assets/media/trafic/3.mp3';

export default {
    name: 'RecordedMotorVehiclesChoiceCar',
    components: { carInput },
    data () {
        return {
            carType: '02',
            carLicense: '',
            carEngineCode: '',
            audioCar: audioCar
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('recordedMotorVehicles', ['setId', 'setCarInfo']),
        getCarInput (data) {
            if (data.carType !== undefined) {
                this.carType = data.carType;
            }
            if (data.carLicense !== undefined) {
                this.carLicense = data.carLicense;
            }
        },
        preStep () {
            this.changeStep('up');
        },
        async nextStep () {
            if (this.carLicense) {
                this.setCarInfo({
                    carType: this.carType,
                    carLicense: this.carLicense
                });
                this.changeStep('down');
            }
        }
    }

};
</script>

<style lang="scss" scoped>
    .wrap-info {
        .detail {
            cursor: pointer;
            color: #4494FF;
        }
    }

    .chooseItem {
        font-size: 20px;
        color: #5FAFEB;
        text-align: center;
    }

    .noChooseItem {
        font-size: 20px;
        color: #fff;
        text-align: center;
    }
</style>
